<template>
<div>
	<div class="warper">
        <section class="views">
	        <a id="back" @click='goback' href="javascript:void(0);">
	        <img src = "https://secure4.jmstatic.com/static_passport/dist/20170419/touch/login_series/images/NavButtonBack_new.png" class = "return"/></a>
	        <h1 class="theme">登录</h1>
	        <a class="top_right" href="/#/reg">注册</a>
		</section>
	    <div id="login-dynamic-form" v-if="mode == 'phone'">
	        <div id="iphone_login">
	            <div class="other-login-outer">
	                <div class="other-login">
	                    <span class="other-title">使用手机登录</span>
	                </div>
	            </div>
	            <div class="jm_row icon_phone">
	                <div class="jm_col">
	                    <input type="text" class="register_input" 
	                    v-model="account"
	                    name="dynamic_mobile" value="" id="dynamic_mobile" placeholder="请输入11位手机号" alertname="手机号">
	                </div>

	            </div>
	            <div class="jm_row">
	                <div class="jm_col">
	                    <input type="tel" class="register_input"
	                    	v-model="password"
	                      name="telephoneN" placeholder="请输入短信验证码" id="dynamic_password" alertname="短信验证码" maxlength="6">
	                </div>
	                <div class="jm_col jm_col_90px">
	                    <a   class="register_yzm"  href="javascript:;">验证</a>
	                </div>
	            </div>
	            <div class="jm_row " id="dynamic_verify_code">
	                <div class="jm_col">
	                <input type="text" maxlength="4" 
	                v-model="code"
	                name="dynamic_hash_code" class="register_input" id="dynamic_hash_code" placeholder="请按右图输入"></div>
	                <div class="jm_col jm_col_90px"> <img id="img_dynamic_hash_code" src="/i/account/hash_code?from=dynamic_login&1499651171" class="check_code"></div>
	            </div>
	            <div class="jm_row register_jumei_account">
	                <a class="jm_col jm_left" id="use_jumei_account" @click="changeLoginMode('normal')">使用故与事帐号登录</a>
	                <div class="jm_col jm_right">30天内自动登录</div>
	            </div>
	        </div>
	        <input type="submit" value="登录" @click="DoLogin" :class="{hover: account != '' && password != '' && code != ''}" class="register_button" id="ga_dynamic_login">
	    </div>
	    <!-- 普通登录 -->
	    <div v-if="mode == 'normal'"
	     id="login" autocomplete="off">
	        <div class="other-login-outer">
	            <div class="other-login">
	                <span class="other-title">使用故与事帐号登录</span>
	            </div>
	        </div>
	        <div class="jm_row icon_phone">
	            <div class="jm_col">
	                <input type="text" class="register_input" 
	                v-model="account"
	                name="email"  id="account" placeholder="已注册的手机号 / 邮箱 / 用户名" alertname="用户名" />
	            </div>
	        </div>
	        <input type="text" style="display: none;">
	        <input type="password" class="register_input"
					v-model="password"
			        name="password" placeholder="6-16位登陆密码" alertname="6-16位登陆密码" autocomplete="new-password">
            <div id="geetest-wrap">
	        </div>
	        <div  id="verify_code">
	            <div class="jm_row">
	                <div class="jm_col">
	                <input type="text" maxlength="4" 
	                v-model="code"
	                name="dynamic_hash_code" class="register_input" id="signup-hashcode-confirm" placeholder="请输入验证码" ></div>
	                <div class="jm_col jm_col_90px"> <img id="code" src="/i/account/hash_code?from=login&1499651171" class="check_code" onclick="change_code()"></div>
	            </div>
	        </div>
            <div class="jm_row register_jumei_account">
	            <a class="jm_col jm_left" id="use_iphone_login" @click="changeLoginMode('phone')">使用手机短信码登录</a>
	            <a class="jm_col jm_right" href="">忘记密码？</a>
	        </div>
	        <input type="button"  value="登录" @click="DoLogin" :class="{hover: account != '' && password != '' && code != ''}" class="register_button" id="ga_login">
	    </div>
	</div>
</div>
</template>

<script>
	
export default {
	name: "Login",
	data(){
		return {
			mode:'phone',
			account:'',
			password:'',
			code:'',
			//是否在提交
			isSubmit:false,
			redirect:''
		}
	},
	mounted(){
		let isLogin = this.$store.state.auth.check();
		console.info(isLogin)
		if(isLogin){
			this.$router.push({path:'/'})
		}
    	this.$store.commit('changeMenu', false);

    	this.redirect = this.$route.query.redirect;
	},
	watch:{

	},
	methods:{
		goback:function(){
	    	window.history.go(-1);
	    },
	    changeLoginMode:function(mode){
	    	this.mode = mode;
	    	this.password="";
	    },
	    DoLogin:function(){

	    	if (!this.account || this.account == '') {
	    		this.$message.error("请输入11位手机号或账号");
	    		return;
	    	}

	    	//公共部分
	    	if (!this.password || this.password == '') {
	    		this.$message.error("请输入账户密码或动态密码");
	    		return;
	    	}

	    	//公共部分
	    	if (!this.code || this.code == '') {
	    		this.$message.error("请输入验证码");
	    		return;
	    	}

	    	if (this.isSubmit) {
	    		return;
	    	}
	    	this.isSubmit = true;
		    this.$http.post(this.$store.state.apidomain +  "m/app/Login",
		    	{UserName: this.account, Password: this.password, code:this.code}).then(
	    	function(respone) {
	    		if (respone.body.status != "failed") {
	    			this.$message({
				      message: '恭喜你，这是一条成功消息',
				      type: 'success'
				    });

				    //todo:做登录
				    this.$store.commit("ACCOUNT_AUTH_STATUS_CHANGED", {jwt_token:respone.body.result.userId, id:respone.body.result.userId});

				    if (!this.redirect || this.redirect == '') {
				    	this.redirect = '/';
				    }

				    this.$router.push(this.redirect);

	    		} else {
	    			this.$message.error(respone.body.result.message);
	    		}

	    		this.isSubmit = false;
	    	}, function(respone){
	    		this.$message.error('网络不给力...');
	    		this.isSubmit = false;
	    	})
	    },
	    validPhone:function(){

	    }
	}
}
</script>
<style>
.warper {
    position: relative;
    top: 0px;
    left: 0px;
    width: 100%;
    z-index: 1;
    background: #fff;
    padding-bottom: 80px;
}.views {
    text-align: left;
    height: 44px;
    position: relative;
    background: #fff;
    margin: 0;
    padding: 0;
    border-bottom: 1px solid #eee;
}#back {
    position: absolute;
    top: 0;
    left: 0;
    width: 40px;
    height: 44px;
}.views .return {
    top: 13px;
    left: 10px;
    width: 7px;
    height: 13px;
    position: absolute;
}section .theme {
    display: block;
    text-align: center;
    margin: 0px 35px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: 17px;
    color: #666;
    height: 44px;
    line-height: 44px;
}.top_right {
    position: absolute;
    line-height: 45px;
    right: 10px;
    top: 0px;
    color: #333;
    font-size: 15px;
}form {
    margin: 0 12%;
}.other-login-outer {
    padding: 0 10%;
}.other-login {
    position: relative;
    height: 58px;
    line-height: 58px;
    text-align: center;
}.other-login:before {
    content: '';
    font-size: 0px;
    border-bottom: solid 1px #f5f5f5;
    position: absolute;
    top: 29px;
    left: 0px;
    width: 100%;
    z-index: -1;
}.other-title {
    padding: 0px 5px;
    background: #fff;
}
.jm_row {
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -moz-flex;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
}.jm_col {
    -webkit-box-flex: 1;
    -webkit-flex: 1;
    -moz-box-flex: 1;
    -moz-flex: 1;
    -ms-flex: 1;
    flex: 1;
    display: block;
    width: 100%;
}.register_input {
    background: #f5f5f5;
    border-radius: 20px;
    width: 100%;
    height: 36px;
    font-size: 14px;
    color: #999999;
    text-align: left;
    text-indent: 21px;
    margin-top: 18px;
}.icon_phone .register_input {
    margin-top: 0;
}
.jm_col_90px {
    -webkit-box-flex: 0;
    -webkit-flex: 0 0 90px;
    -moz-box-flex: 0;
    -moz-flex: 0 0 90px;
    -ms-flex: 0 0 90px;
    flex: 0 0 90px;
    max-width: 90px;
    margin: 18px 0 0 10px;
}.register_yzm {
    background: #ffffff;
    border: 1px solid #feb2c5;
    border-radius: 20px;
    width: 97%;
    height: 35px;
    font-size: 14px;
    color: #feb2c5;
    line-height: 35px;
    text-align: center;
    display: block;
}#code, .check_code {
    height: 35px;
    vertical-align: middle;
}.register_jumei_account {
    font-size: 12px;
    height: 32px;
    line-height: 44px;
}.register_jumei_account .jm_left {
    color: #fe4070;
    padding-left: 7px;
}.jm_left {
    text-align: left;
}.register_jumei_account .jm_right {
    color: #999999;
    padding-right: 7px;
}.jm_right {
    text-align: right;
}.register_button {
    background: #feb2c5!important;
    border-radius: 20px;
    width: 100%;
    height: 36px;
    font-size: 14px;
    color: #ffffff;
    line-height: 14px;
    text-align: center;
    margin: 18px auto 0 auto;
    display: block;
}

.register_button.hover{
    background:#fe4070!important;
}
</style>


